<template>
  <div class="Journal-box">
    <div class="Journal">
      <div class="Journal-form">
        <h1>{{ $t("Journal.formdata.title") }}</h1>
        <p
          v-for="(item, index) in $t('Journal.formdata.describe')"
          :key="index"
        >
          {{ item }}
        </p>
      </div>

      <div class="Journal-content">
        <el-form
          ref="elForm"
          :model="formData"
          size="medium"
          label-width="100px"
          label-position="top"
        >
          <el-form-item
            :label="$t('Journal.formdata.identityLable')"
            prop="identity"
          >
            <el-radio v-model="formData.identity" label="1">{{
              $t("Journal.formdata.label1")
            }}</el-radio>
            <el-radio v-model="formData.identity" label="2">{{
              $t("Journal.formdata.label2")
            }}</el-radio>
          </el-form-item>
          <el-form-item
            :label="$t('Journal.formdata.contentLable')"
            prop="content"
          >
            <el-select
              v-model="formData.content"
              placeholder="请选择"
              style="width: 100%"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('Journal.formdata.nameLable')" prop="name">
            <el-input
              v-model="formData.name"
              placeholder=""
              clearable
              :style="{ width: '100%' }"
            >
            </el-input>
          </el-form-item>
          <el-form-item
            :label="$t('Journal.formdata.mailboxLable')"
            prop="mailbox"
          >
            <el-input
              v-model="formData.mailbox"
              placeholder=""
              clearable
              :style="{ width: '100%' }"
            >
            </el-input>
          </el-form-item>
        </el-form>
        <div class="button">
          <el-button type="danger" round @click="submit">
            {{ $t("Journal.formdata.buttonText") }}
          </el-button>
        </div>
      </div>
    </div>
    <div class="Journal-list">
      <p class="Journal-text">{{ $t("Journal.text") }}</p>
      <div class="Journal-view">
        <div
          class="Journal-item"
          v-for="(item, index) in JournalList"
          :key="index"
          @click="onDateils(item)"
        >
          <img :src="'./static/image/' + item.img" alt="" />
          <div>
            <p :style="item.status == 0 ? 'background: rgba(0,0,0,0.5);' : ''">
              {{ item.count }}
            </p>
            <p>{{ item.title }}</p>
            <p>{{ item.describe }}</p>
            <p>{{ item.more }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Loading, Message } from "element-ui";
export default {
  data: () => {
    return {
      formData: {
        identity: "",
        name: "",
        mailbox: "",
        content: "",
      },

      radio: "",
      JournalList: [],
      list: [],
    };
  },
  computed: {
    options() {
      return this.$t("Journal.formdata.options");
    },
    formMassge() {
      return this.$t("Journal.formdata.formMassge");
    },
  },

  mounted() {
    console.log(this.options, "====");
    this.intData();
  },
  methods: {
    intData(id) {
      const lang = localStorage.getItem("lang");
      switch (lang) {
        case "en":
          axios.get("/static/lang/en.json", {}).then(({ data }) => {
            this.list = data.JournalList;
            this.JournalList = this.list.filter((item) => item.status == 1);
          });
          break;
        case "Czh":
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/Czh.json", {}).then(({ data }) => {
            this.list = data.JournalList;
            this.JournalList = this.list.filter((item) => item.status == 1);
          });

          break;

        default:
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/zh.json", {}).then(({ data }) => {
            this.list = data.JournalList;
            this.JournalList = this.list.filter((item) => item.status == 1);
          });

          break;
      }
    },
    onDateils(item) {
      window.scrollTo({ top: 0 });
      if (item.status == 1) {
        this.$router.push({ path: "/Journal", query: { id: item.id } });
      } else {
        this.$router.push("/journalform");
      }
    },

    submit() {
      const { identity, name, mailbox, content } = this.formData;
     
      if (identity && name && mailbox && content) {
        console.log(this.fileList);
        let formdata = new FormData();

        formdata.append("身份", identity == 1 ? "内容提供者" : "读者");
        formdata.append("内容", content);
        formdata.append("名称", name);
        formdata.append("电子邮箱", mailbox);
        const loadingInst = Loading.service({
          lock: true,
          text: "loading...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.5)",
        });

        axios({
          method: "post", //必须post请求
          url: periodicalMailUrl, //test换成你要接收的邮箱地址
          async: true,
          data: formdata,
          headers: {
            "Content-Type": "text/html; charset=utf-8",
          },
        })
          .then((response) => {
            loadingInst.close();
            console.log(response, "====");
            this.$router.push("/success");
          })
          .catch((err) => {
            loadingInst.close();
            that.$message({
              showClose: true,
              message: "error",
              type: "warning",
              offset: 80,
            });
          });
      } else {
        console.log(this.form);

        this.$message({
          showClose: true,
          message: this.formMassge,
          type: "warning",
          offset: 80,
        });
      }
    },
  },
};
</script>
